import axios from "axios";
import React, { useRef, useState } from "react";
import { useHistory } from "react-router-dom";
import LoadingSpinnerWhite from "../loadingSpinner/loadingSpinnerWhite/loadingSpinnerWhite";
import styles from "./loginPage.module.css";
import Cookies from "universal-cookie";

const cookies = new Cookies();

const LoginPage = (props) => {
  const history = useHistory();
  const idRef = useRef();
  const pwRef = useRef();
  const [loadingOn, setLoadingOn] = useState(false);

  const axiosInstance = axios.create({
    withCredentials: true,
  });

  const setCookie = (cookie) => {
    axiosInstance.defaults.headers.Cookies = JSON.parse(cookie);
  };

  const loginSubmitHandler = async (e) => {
    e.preventDefault();
    setLoadingOn(true);
    const id = idRef.current.value;
    const password = pwRef.current.value;

    axios
      .post(
        `${process.env.REACT_APP_BASEURL}/user/login`,
        {
          id,
          password,
        },
        { withCredentials: true }
      )
      .then((response) => {
        console.log(response);
        const sessionId = response.headers["Set-Cookie"];
        //cookies.set("JSESSIONID", sessionId, { path: "/" });
        console.log(response.headers);
        console.log(response.headers["Set-Cookie"]);
        console.log(cookies.get("JSESSIONID"));
        if (response.data === "loggedin") {
          //sessionStorage.setItem("id", sessionId);
          console.log(response);
          window.alert(" .");
        } else {
          window.alert("   ");
        }
        setLoadingOn(false);
      })
      .catch((err) => {
        setLoadingOn(false);
        console.error(err);
      });
  };
  return (
    <section className={styles.login_page}>
      <button
        className={styles.test}
        onClick={() => {
          axios
            .post(`${process.env.REACT_APP_BASEURL}/user/session`)
            .then((response) => {
              //console.log(sessionStorage.getItem("id"));
              console.log(response);
              //console.log(document.cookie);
            })
            .catch((err) => console.error(err));
        }}
      >
        
      </button>
      <button
        className={styles.test}
        onClick={() => {
          axios
            .post(`${process.env.REACT_APP_BASEURL}/user/logout`)
            .then(() => {
              console.log("");
            })
            .catch((err) => console.error(err));
        }}
      >
        
      </button>
      <section className={styles.container}>
        <p className={styles.title}></p>
        <form className={styles.main}>
          <div className={styles.input_container}>
            <input
              ref={idRef}
              type="text"
              className={styles.input}
              placeholder=""
              spellCheck="false"
            />
            <input
              ref={pwRef}
              type="password"
              className={styles.input}
              placeholder=""
              spellCheck="false"
            />
          </div>
          <button className={styles.submit_button} onClick={loginSubmitHandler}>
            {loadingOn ? <LoadingSpinnerWhite /> : ""}
          </button>
        </form>
        <div className={styles.id_save_find_container}>
          <div className={styles.id_save_container}>
            <input type="checkbox" className={styles.id_save_checkbox} />
            <span className={styles.id_save_text}> </span>
          </div>
          <div className={styles.id_pw_find_container}>
            <button
              className={`${styles.id_pw_find_button} ${styles.left}`}
              onClick={() => {
                history.push("/findID");
              }}
            >
              
            </button>
            |
            <button
              className={styles.id_pw_find_button}
              onClick={() => {
                history.push("/findPW");
              }}
            >
              
            </button>
          </div>
        </div>
        <div className={styles.sns_login_container}>
           SNS 
        </div>
        <div className={styles.signup_container}>
          <span className={styles.signup_message}>
                  .
          </span>
          <button
            className={styles.go_signup_button}
            onClick={() => {
              history.push("/signup");
              window.scrollTo({ top: 0 });
            }}
          >
            
          </button>
        </div>
      </section>
    </section>
  );
};

export default LoginPage;
